import React from 'react'
import axios from 'axios'
import FormContent from './FormContent'

class CreateForm extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            title: '',
            place: '',
            date: '',
            description: '',
            comments: '',
            picture: null,
            
            validity: {
              title: false,
              place: false,
              date: false,
              description: false,
            },
            
            errors: {
              title: "Field cannot be blank!",
              place: "Field cannot be blank!",
              date: "Field cannot be blank!",
              description: "Field cannot be blank!",
            },
        }
        this.handleValueChange = this.handleValueChange.bind(this)
        this.handleReset = this.handleReset.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)
        this.handleImageChange = this.handleImageChange.bind(this)
    }
    
    checkValidity(type, value) {
        
        let validity = {
            title: this.state.validity.title,
            place: this.state.validity.place,
            date: this.state.validity.date,
            description: this.state.validity.description,
        }
        
        validity[type] = (value.trim() !== "")
        
        let errors = {
            title: this.state.errors.title,
            place: this.state.errors.place,
            date: this.state.errors.date,
            description: this.state.errors.description,
        }
        
        if (validity[type]) {
            errors[type] = ""
        } else {
            errors[type] = "Field cannot be blank!"
        }
        
        this.setState({validity: validity})
        this.setState({errors: errors})
    }
    
    handleValueChange(e) {
        let value = e.target.value
        let type = e.target.name
        let newState = {[type] : value}
        
        this.setState(newState)
        
        if (type !== "comments"){
          this.checkValidity(type, value)
        }
    }
    
    handleImageChange = (e) => {
      this.setState({
        picture: e.target.files[0]
      })
    }
    
    handleReset(e) {
        this.setState({
            title: '',
            place: '',
            date: '',
            description: '',
            comments: '',
            picture: null,
            
            validity: {
              title: false,
              place: false,
              date: false,
              description: false,
            },
            
            errors: {
              title: "Field cannot be blank!",
              place: "Field cannot be blank!",
              date: "Field cannot be blank!",
              description: "Field cannot be blank!",
            },
        })
        
        let fileInput = document.getElementById('picturecreateForm')
        fileInput.value = null
    }
    
    handleSubmit(e) {
        let newData = {
            "title" : this.state.title,
            "place" : this.state.place,
            "date" : this.state.date,
            "description" : this.state.description,
            "comments" : this.state.comments,
            "picture" : this.state.picture
        }

        let form_data = new FormData()
        form_data.append('title', newData.title)
        form_data.append('place', newData.place)
        form_data.append('date', newData.date)
        form_data.append('description', newData.description)
        form_data.append('comments', newData.comments)
        
        if (this.state.picture) {
          form_data.append('picture', newData.picture)
        }
        
        let config={
            url: 'https://eventblog12231.herokuapp.com/dataapis/token-auth-refresh/',
            method: 'POST',
            headers:{"Content-Type" : "application/json"},
            data: {"refresh" : localStorage.getItem('refresh') }
        }
            
        axios(config)
        .then(res => {
            return res.data.access
        })
        .then(access=>{
            return axios({
                url: "https://eventblog12231.herokuapp.com/dataapis/outdoordate/",
                method: "POST",
                data: form_data,
                headers:{ 
                  'Authorization': `Bearer ${access}`,
                  'content-type': 'multipart/form-data',
                }
            })
        })
        .then(()=> this.props.refreshList())
        .then(()=> this.handleReset())
        .catch(err => console.log(err))
    }
    
    render() {
        let formInvalid=(Object.values(this.state.validity).indexOf(false) > -1)?true:false
        
        return(
            <FormContent 
              title={this.state.title}
              place={this.state.place}
              date={this.state.date}
              description={this.state.description}
              comments={this.state.comments}
              picture={this.state.picture}
              onValueChange={this.handleValueChange}
              onReset={this.handleReset}
              onImageChange={this.handleImageChange}
              id="createForm"
              
              validity={this.state.validity}
              errors={this.state.errors}
            >
              <button 
                type="button" 
                className="btn btn-primary" 
                onMouseDown={e=>e.preventDefault()} 
                onClick={this.handleSubmit} 
                disabled={formInvalid} 
                data-toggle="collapse" 
                data-target={'#' + this.props.id}
               >
                  Create
              </button>
            </FormContent>
        )
    }
}

export default CreateForm