import React from 'react'
import axios from 'axios'

class ConfirmDelete extends React.Component {
    constructor(props){
        super(props)
        this.handleDelete = this.handleDelete.bind(this)
    }
    
    
    handleDelete(e) {
        let config={
            url: 'https://eventblog12231.herokuapp.com/dataapis/token-auth-refresh/',
            method: 'POST',
            headers:{"Content-Type" : "application/json"},
            data: {"refresh" : localStorage.getItem('refresh') }
        }
            
        axios(config)
        .then(res => {
            return res.data.access
        })
        .then(access=>{
            axios({
                url: "https://eventblog12231.herokuapp.com/dataapis/outdoordate/" + this.props.item.pk + '/',
                method: 'DELETE',
                headers:{ 'Authorization': `Bearer ${access}`},
            })
        })
        .then(()=> this.props.refreshList())
        .catch(err=>{console.log(err)})
    }
    
    render(){
        return (
          <div className="row"><div className="col">
            <p>Delete {this.props.item.title}?</p>
            <button name={this.props.item.pk} className="btn btn-danger" onClick={this.handleDelete} data-dismiss="modal">
              Confirm Delete
            </button>
          </div></div>
        )
    }
}

export default ConfirmDelete