import React from 'react'
import { Formik, Form, Field, ErrorMessage } from 'formik'

function SignupForm(props){
    
    let parentHandleSignup = props.handleSignup
    
    let loginErrorMessage
    if (props.error) {
      loginErrorMessage = <p className="text-danger">{props.error}</p>
    }
    
    return (
        <div className="container border border-primary rounded my-4">
          <div className="row px-4 pt-4">
            <div className="col">
              <h2>Signup</h2>
            </div>
          </div>
          <div className="row p-4">
            <div className="col">
                <Formik
                  initialValues={{ username: '', password: '' }}
                  
                  validate={values => {
                    let errors = {}
                    
                    if (!values.username) {
                      errors.username = 'Required'
                    }
                    
                    if ( !values.password ) {
                      errors.password = 'Required'
                    }
                    
                    return errors;
                  }}
                  
                  onSubmit={(values, {setSubmitting})=> {
                    parentHandleSignup(values)
                    setSubmitting(false);
                  }}
                >
                {({ touched, errors, isSubmitting }) => (
                    <Form>
                    
                      <div className="form-group">
                        <label htmlFor="signupUsername">Username:</label>
                        <Field type="text" id="signupUsername" name="username" className={`form-control ${touched.username && errors.username ? "is-invalid" : ""}`}/>
                        <ErrorMessage name="username" component="div" className="text-danger"/>
                      </div>
                      
                      <div className="form-group">
                        <label htmlFor="signupPassword">Password:</label>
                        <Field type="password" id="signupPassword" name="password" className={`form-control ${touched.password && errors.password ? "is-invalid" : ""}`}/>
                        <ErrorMessage name="password" component="div" className="text-danger"/>
                      </div>
                      
                      {loginErrorMessage}
                      
                      <button type="submit" disabled={isSubmitting} className="btn btn-primary my-4">
                        Signup
                      </button>
                      
                    </Form>
                )}
                </Formik>
            </div>
          </div>
        </div>
    )
}

export default SignupForm