import React from 'react'
import Modal from './Modal'
import ModalButton from './ModalButton'
import ContentItemDetails from './ContentItemDetails'
import EditForm from './EditForm'
import ConfirmDelete from './ConfirmDelete'

function ContentItem(props) {
    return (
        <div className="col-lg-6 col-12">
        <div className="row"><div className="col border border-primary rounded m-4">
        
        <div className="row px-4 pt-4">
          <div className="col-12">
            <p>Title: {props.item.title}</p>
          </div>
         
         <div className="col-12">
            <p>Date: {props.item.date}</p>
          </div>
          
          <div className="col-12">
            <p>Place: {props.item.place}</p>
          </div>
        </div>
          
        <div className="row mb-4 px-4">
            <div className="col-12">
              <ModalButton id={props.item.pk} buttonColor="primary">
                {"Details"}
              </ModalButton>

              <ModalButton id={props.item.pk + 'edit'} buttonColor="secondary">
                {"Edit"}
              </ModalButton>
              
              <ModalButton id={props.item.pk + 'del'} buttonColor="danger pull-right">
                {"Delete"}
              </ModalButton>
            </div>
              
              <Modal id={props.item.pk} title={props.item.title}>
                <ContentItemDetails item={props.item}/>
              </Modal>
              
              <Modal id={props.item.pk + 'edit'} title={'Edit ' + props.item.title}>
                <EditForm item={props.item} refreshList={props.refreshList}/>
              </Modal>
              
              <Modal id={props.item.pk + 'del'} title={"Delete " + props.item.title}>
                <ConfirmDelete item={props.item} refreshList={props.refreshList} />
              </Modal>
        </div>
        
        </div></div>
        </div>
    )
}

export default ContentItem