import React from 'react'

function CreateTab(props) {
    return (
        <div className="row border border-primary rounded my-4 p-4">
        
        <div className="col my-auto">
          <h2>{props.title}</h2>
        </div>
        
        
        <div className="col-3 text-right my-auto">
          <button onMouseDown={e=>e.preventDefault()} className="btn btn-primary" type="button" data-toggle="collapse" data-target={'#' + props.id} aria-expanded="false" aria-controls={'#' + props.id}>
            {props.buttonText}
          </button>
        </div>
        
        <div className="col-12 collapse" id={props.id}>
        {props.children}
        </div>
        </div>
    )
}

export default CreateTab