import React from 'react'

function ModalButton(props) {
    
    let className = "btn mr-1 btn-" + props.buttonColor
    
    return (
         <button type="button" className={className} data-toggle="modal" data-target={"#"+ props.id} onMouseDown={e=>e.preventDefault()}>
          {props.children}
         </button>
    )
}

export default ModalButton