import React from 'react'

function ContentFilter(props) {
    let pageRange = props.pageRange.map(data=>(<option value={data}>{data}</option>))
    
    
    let pageWidget = null
    if (pageRange.length > 1) {
        pageWidget = (
          <div className="col">
            <div className="form-group">
              <label>Page: </label>
              <select className="form-control" value={props.page} onChange={props.onPageChange}>
                {pageRange}
              </select>
            </div>
          </div>
        )
    }
    
    return (
        <div className="row border border-primary rounded mb-4"><div className="col">
        
        <div className="row m-4">
          <div className="col-12 col-lg-6">
            <div className="form-group">
              <label for="titleFilter">Filter by title:</label>
              <input className="form-control" type="text" id="titleFilter" value={props.titleFilter} onChange={props.onFilterChange} />
            </div>
          </div>
          
          <div className="col-12 col-lg-6">
            <div className="form-group">
              <label for="placeFilter">Filter by place:</label>
              <input className="form-control" type="text" id="placeFilter" value={props.placeFilter} onChange={props.onFilterChange} />
            </div>
          </div>
          
          <div className="col">
            <div className="form-group">
              <label for="monthFilter">Filter by month:</label>
              <input className="form-control" type="month" id="monthFilter" value={props.monthFilter} onChange={props.onFilterChange} />
            </div>
          </div>
          
          <div className="col">
            <div className="form-group">
              <label for="dateFilter">Filter by date:</label>
              <input className="form-control" type="date" id="dateFilter" value={props.dateFilter} onChange={props.onFilterChange} />
            </div>
          </div>
          
          {pageWidget}
          
        </div>
          
        <div className="row m-4">
          <div className="col">
            <button type="button" className="btn btn-primary m-1" onClick={props.onClick} onMouseDown={e=>e.preventDefault()}>Filter</button>
            <button type="button" className="btn btn-secondary m-1" onClick={props.onReset} onMouseDown={e=>e.preventDefault()}>Reset</button>
          </div>
        </div>
        
        </div></div>
    )
}

export default ContentFilter