import React from 'react'

function ContentItemDetails(props) {
    return (
      <div className="row justify-content-center">  
        <div className="col-12 m-4">
          <img className="img-fluid mx-auto d-block" src={props.item.picture}/>
        </div>
        
        <div className="col-6 p-4">
          <p>Date: {props.item.date}</p>
        </div>
        
        <div className="col-6 p-4">
          <p>Place: {props.item.place}</p>
        </div>
        
        <div className="col-12 p-4">
          <p>Description: {props.item.description}</p>
        </div>
        
        <div className="col-12 p-4">
          <p>Comments: {props.item.comments}</p>
        </div>
      </div>  
    )
}

export default ContentItemDetails