import React from 'react'

function FormContent(props){
    
    return (
      <div className="row"><div className="col">
      
        <div className="row">
          <div className="col-12">
            <div className="form-group">
              <label for={"title" + props.id}>Title</label>
              <input name="title" onChange={props.onValueChange} value={props.title} type="text" id={"title" + props.id} className={"form-control "+ (props.validity.title?"":"is-invalid")} />
              <p className="text-danger">{props.errors.title}</p>
            </div>
          </div>
          
          <div className="col-12">
            <div className="form-group">
              <label for={"place" + props.id}>Place</label>
              <input name="place" onChange={props.onValueChange} value={props.place} type="text" id={"place" + props.id} className={"form-control "+ (props.validity.place?"":"is-invalid")} />
              <p className="text-danger">{props.errors.place}</p>
            </div>
          </div>
          
          <div className="col-12">
            <div className="form-group">
              <label for={"date" + props.id}>Date</label>
              <input name="date" onChange={props.onValueChange} value={props.date} type="date" id={"date" + props.id} className={"form-control "+ (props.validity.date?"":"is-invalid")} />
              <p className="text-danger">{props.errors.date}</p>
            </div>
          </div>
          
          <div className="col-12">
            <div className="form-group">
              <label for={"description" + props.id}>Description</label>
              <textarea name="description" onChange={props.onValueChange} value={props.description} type="textarea" id={"description" + props.id} className={"form-control "+ (props.validity.description?"":"is-invalid")} />
              <p className="text-danger">{props.errors.description}</p>
            </div>
          </div>
          
          <div className="col-12">
            <div className="form-group">
              <label for={"comments" + props.id}>Comments</label>
              <textarea name="comments" onChange={props.onValueChange} value={props.comments} type="textarea" id={"comments" + props.id} className="form-control" />
            </div>
          </div>
          
          <div className="col-12">
            <div className="form-group">
              <label for={"picture" + props.id}>Picture</label>
              <input onChange={props.onImageChange} type="file" id={"picture" + props.id} accept="image/png, image/jpeg" className="custom-file" />
            </div>
          </div>
        </div>
        
        <div className="row mt-4">
          <div className="col">
            {props.children}
            <button type="button" className="btn btn-secondary ml-1" onMouseDown={e=>e.preventDefault()} onClick={props.onReset}>Reset</button>
          </div>
        </div>
      </div></div>
    )
}

export default FormContent