import React from 'react'

function UserDetailBar(props) {
    
    return (
        <div className="container">
          <div className="row">
            <div className="col-auto">
              <p className="my-auto">Current User: <span className="text-primary">{props.currentUser}</span></p>
            </div>
            <div className="col-auto">
              <button type="button" className="btn btn-sm btn-secondary my-auto" onClick={props.onLogout}>
                logout
              </button>
            </div>
          </div>
        </div>
    )

}

export default UserDetailBar