import React from 'react';
import axios from 'axios'
import ContentDisplay from './Components/ContentDisplay'
import UserDetailBar from './Components/UserDetailBar'
import UsernamePasswordForm from './Components/UsernamePasswordForm'
import SignupForm from './Components/SignupForm'


class AppLoginControl extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            username: '',
            loginError: '',
            signupError: '',
        }
        this.handleLogin = this.handleLogin.bind(this)
        this.handleLogout = this.handleLogout.bind(this)
        this.handleSignup = this.handleSignup.bind(this)
    }
    
    componentDidMount() {
       
        let config={
            url: 'https://eventblog12231.herokuapp.com/dataapis/token-auth-refresh/',
            method: 'POST',
            headers:{"Content-Type" : "application/json"},
            data: {"refresh" : localStorage.getItem('refresh') }
        }
            
        axios(config)
        .then(res => {
            return res.data.access
        })
        .then(access=>{
            return axios({
                url: 'https://eventblog12231.herokuapp.com/dataapis/current-user',
                headers:{ 'Authorization': `Bearer ${access}`}
            })
        })
        .then(res => {
            this.setState({username: res.data.username})
        })
    }
    
    handleLogin(data) {
        
        let config = {
            url: 'https://eventblog12231.herokuapp.com/dataapis/token-auth-pair/',
            method: 'POST',
            headers: {'Content-Type': 'application/json'},
            data: data
        }
        
        axios(config)
        .then(res=>{
            localStorage.setItem('refresh', res.data.refresh)
            return axios({
                url: 'https://eventblog12231.herokuapp.com/dataapis/current-user',
                headers:{ 'Authorization': `Bearer ${res.data.access}`}
            })
        })
        .then(res => {
            this.setState({
                username: res.data.username,
                signupError:'', 
                loginError: '',
            })
        })
        .catch(()=>{
            this.setState({ loginError: 'Username or Password incorrect!' })    
        })

    }
    
    handleSignup(data) {
        axios.post('https://eventblog12231.herokuapp.com/dataapis/users', data)
        .then(res => {
          localStorage.setItem('refresh', res.data.token.refresh)
          this.setState({
              username : res.data.username, 
              signupError:'', 
              loginError: ''
          })          
        })
        .catch(err=>{
            this.setState({signupError: 'Username already exists!'})
        })
    }
    
    handleLogout() {
        localStorage.removeItem('access')
        localStorage.removeItem('refresh')
        this.setState({ username: ''})
    }
    
    render() {
        let content
        if (this.state.username) {
            content = (
              <div>
                <UserDetailBar currentUser={this.state.username} onLogout={this.handleLogout}/>
                <ContentDisplay />
              </div>
            )
        } else {
            content = (
              <div>
                <UsernamePasswordForm handleLogin={this.handleLogin} error={this.state.loginError}/>
                <SignupForm handleSignup={this.handleSignup} error={this.state.signupError} />
              </div>
            )
        }
        
        return(
          <div>
            {content}
          </div>
        )
    }
}

export default AppLoginControl;
