import React from 'react'
import axios from "axios"
import CreateTab from './CreateTab'
import ContentFilter from './ContentFilter'
import ContentItem from './ContentItem'
import CreateForm from './CreateForm'

class ContentDisplay extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            titleFilter: '',
            placeFilter: '',
            dateFilter: '',
            monthFilter: '',
            resultsPerPage: 100,
            page: 1,
            totalResults: 0,
            queryset: []
        }
        this.handleFilterChange = this.handleFilterChange.bind(this)
        this.refreshList = this.refreshList.bind(this)
        this.handleReset = this.handleReset.bind(this)
        this.handlePageChange = this.handlePageChange.bind(this)
    }
    
    componentDidMount() {
        this.refreshList()
    }
    
    refreshList(){
        let queryStr = "https://eventblog12231.herokuapp.com/dataapis/outdoordate/?page=" + this.state.page + '&'        
        
        if (this.state.titleFilter) {
            queryStr += 'title=' + this.state.titleFilter + '&'
        }
        
        if (this.state.dateFilter) {
            queryStr += 'date=' + this.state.dateFilter + '&'
        } else if (this.state.monthFilter) {
            queryStr += 'date=' + this.state.monthFilter + '&'
        }
        
        if (this.state.placeFilter) {
            queryStr += 'place=' + this.state.placeFilter + '&'
        }
          
        let config={
            url: 'https://eventblog12231.herokuapp.com/dataapis/token-auth-refresh/',
            method: 'POST',
            headers:{"Content-Type" : "application/json"},
            data: {"refresh" : localStorage.getItem('refresh') }
        }
            
        axios(config)
        .then(res => {
            return res.data.access
        })
        .then(access=>{
            return axios({
                url: queryStr,
                headers:{ 'Authorization': `Bearer ${access}`}
            })
        })
        .then(res => this.setState({ queryset: res.data.results, totalResults: res.data.count }))
        .catch(err => console.log(err))
    }
        
    handleFilterChange(e) {
        let value = e.target.value
        let type = e.target.id
        let newState = {[type] : value}
        
        if (type==='monthFilter') {
            newState['dateFilter'] = ''
        } else if (type==='dateFilter') {
            newState['monthFilter'] = ''
        }
        
        this.setState(newState)
    }
    
    handleReset(e) {
        this.setState({
            titleFilter: '',
            placeFilter: '',
            dateFilter: '',
            monthFilter: '',
            page: 1,
        }, ()=>this.refreshList())
    }
    
    handlePageChange(e){
        let page = e.target.value
        this.setState({page: page})
    }
    
    render(){
        let list_items
        if (this.state.queryset.length === 0 ) {
            list_items = <div className="col text-center pb-4"><h3>Nothing here!</h3></div>
        } else {
            list_items = this.state.queryset.map(data => <ContentItem key={data.pk} item={data} refreshList={this.refreshList}/>)
        }
        let pageRange = Array.from({length: Math.ceil(this.state.totalResults/100)}, (v, k) => k+1); 
        
        return (
          <div className="container">
            
            <CreateTab id="createForm" buttonText="Show" title="Create new entry">
              <CreateForm id="createForm" refreshList={this.refreshList}/>
            </CreateTab>
          
            <ContentFilter 
              onFilterChange={this.handleFilterChange} 
              onPageChange={this.handlePageChange}
              titleFilter={this.state.titleFilter}
              placeFilter={this.state.placeFilter}
              dateFilter={this.state.dateFilter}
              monthFilter={this.state.monthFilter}
              page={this.state.page}
              pageRange={pageRange}
              onClick={this.refreshList}
              onReset={this.handleReset}
            />
            
            <div class="row justify-content-between mx-4">
            { list_items }
            </div>
            
          </div>
        )
    }
}

export default ContentDisplay